main {
  section.hero {
    $bg-opacity: 0.7;
    background: {
      image: linear-gradient(
          rgba(0, 0, 0, $bg-opacity),
          rgba(0, 0, 0, $bg-opacity)
        ),
        url("~/public/hero.jpg");
      position: center;
      repeat: no-repeat;
      size: cover;
    }
    height: 100vh;
    width: 100vw;
    padding-inline: 40px;
    @media (max-width: 466px) {
      padding-inline: 15px;
    }
    .overlay {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      @media (max-width: 615px) {
        justify-content: center;
      }
      .content {
        max-width: 90%;
        h1 {
          text-align: right;
          font-size: 40px;
          margin-bottom: 5px;
          // color: #203f76;
          color: white;
          @media (max-width: 751px) {
            font-size: 35px;
          }
          @media (max-width: 615px) {
            text-align: center;
          }
        }
        p {
          font-size: 22px;
          margin-top: 0;
          width: 909px;
          // color: #0843a9;
          color: white;
          max-width: 100%;
          margin: {
            inline: auto;
          }
          @media (max-width: 751px) {
            font-size: 18px;
          }
          @media (max-width: 615px) {
            text-align: center;
          }
          @media (max-width: 400px) {
            font-size: 15px;
          }
        }
        a {
          @media (max-width: 615px) {
            display: block;
            margin-inline: auto;
            width: fit-content;
          }
        }
      }
    }
  }
}
