@use "./components/navbar";
@use "./components/hero";
@use "./components/about";
@use "./components/contact";
@use "./components/footer";
@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;500;600;700;800;900;1000&display=swap");

*,
*::before,
*::after {
  font-family: "Cairo", sans-serif;
  box-sizing: border-box;
}

%rm-overflow-x {
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  background-color: #fbfbfb;
  @extend %rm-overflow-x;
}

html {
  @extend %rm-overflow-x;
  scroll-behavior: smooth;
}

.btn {
  // background-color: #fd5528;
  // color: #22427d;
  background-color: #22427d;
  color: #fd5528;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 16px;
  text-decoration: none;
  transition: all 300ms linear;
  &:hover {
    background-color: #fd5528;
    color: #22427d;
  }
}
