main {
  section.about {
    height: 100vh;
    @media (max-width: 1130px) {
      height: fit-content;
    }
    max-height: fit-content;
    display: flex;
    justify-content: space-between;
    padding-inline-start: 40px;
    flex-direction: row-reverse;
    margin-bottom: 20px;
    img {
      width: 50%;
      height: 100%;
      object-fit: cover;
      object-position: right;
      @media (max-width: 1130px) {
        display: none;
      }
    }
    h2 {
      margin-top: 50px;
    }
    p {
      padding-inline-end: 40px;
      line-height: 1.6;
      font-size: 18px;
      span {
        color: red;
      }
    }
  }
}
