main {
  section.contact {
    display: flex;
    justify-content: space-between;
    // padding-inline-end: 40px;
    margin-block: 70px;
    img {
      width: 50%;
      height: 100%;
      object-fit: cover;
      object-position: right;
    }
    div {
      margin-inline: auto;
      max-width: 100%;
      h2 {
        font-size: 20px;
        text-align: center;
      }
      form {
        max-width: 100%;
        input {
          border-radius: 10px;
          height: 52px;
          width: 486px;
          max-width: 90%;
          padding-inline: 14px;
          margin-inline: auto;
          border: 1px solid rgb(207, 202, 199);
          margin-bottom: 9px;
          outline: none;
          transition: border 300ms linear;
          display: block;
          &:focus {
            border: 1px solid rgb(255, 255, 255);
          }
        }
        button {
          display: block;
          margin-inline: auto;
        }
      }
    }
  }
}
